import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState(['themes']),
    theme(){
      return this.themes[this.$route.name]
     
    },
    coverStyle(){
      return {
        'background-color': this.theme.color,
        'background-image': `url("${this.theme.cover}")`
      }
    }
  }
}



