import {mapState,mapMutations} from 'vuex'
import $ from 'jquery'
import slick from 'slick-carousel'
export default {
  data() {
      return {
          slickOptions: {
              slidesToShow: 1,
              arrows: false,
              // Any other options that can be got from plugin documentation
          },
          slides: [
            {
              title: "...",
              description: "",
              cover: "http://zashare.org/img/expo/2017/2.jpg"
            },{
              title: "...",
              description: "",
              cover: "http://zashare.org/img/expo/2017/3.jpg"
            },{
              title: "",
              description: "",
              cover: "http://zashare.org/img/expo/2017/4.jpg"
            },{
              title: "...",
              description: "",
              cover: "http://zashare.org/img/expo/2017/5.jpg"
            }
          ],
          currentSlideId: 0
      };
  },
  methods: {
      next() {
          $(".slick").slick("next");
      },

      prev() {
          $(".slick").slick("prev");
      },

      // reInit() {
      //     // Helpful if you have to deal with v-for to update dynamic lists
      //     this.$nextTick(() => {
      //         $(".slick").slick.reSlick();
      //     });
      // },
      
  },
  mounted(){
    this.$nextTick(() => {
      $(".slick").slick(
        this.slickOptions
      )
      let _this=this
      $(".slick").on('beforeChange', function(event, slick, currentSlide, nextSlide){
        console.log(nextSlide)
        _this.currentSlideId=nextSlide
      })

    });
     
    
// $(".slider").slick({
    // $(this.$el).children(".slick").on('beforeChange', function(event, slick, currentSlide, nextSlide){
    //   console.log(nextSlide);
      
    // });
 
  },
  computed: {
    currentSlide(){
      return this.slides[this.currentSlideId]
    }
  }
}



